<template>
  <Card title="上传数额承诺">
    <div class="grid-cols">
      <el-form
        label-position="left"
        ref="uploadForm"
        :rules="uploadRules"
        :model="form"
        label-width="100px"
      >
        <el-form-item prop="pid" label="物品id">
          <el-input v-model="form.pid" placeholder="物品id"></el-input>
        </el-form-item>
        <el-form-item prop="value" label="数额">
          <el-input v-model="form.value" placeholder="数额"></el-input>
        </el-form-item>
        <el-form-item prop="tags" label="标签">
          <el-input v-model="form.tags" placeholder="城市 系统 业务 备注（空格隔开）"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="upload">上传承诺到服务器</el-button>
        </el-form-item>
      </el-form>
    </div>
  </Card>
</template>

<script>
// @ is an alias to /src
import Card from "@/components/Card.vue";
import { Message, Notification } from "element-ui";
import { numberApi } from "@/api/numbers";
import { getters } from "@/store/store";

export default {
  name: "Upload",
  components: {
    Card,
  },
  data() {
    return {
      form: {
        value: "",
        tags: "",
        pid: "",
      },
      options: ["shanghai", "myc", "edu", "test"],
      uploadRules: {
        value: [{ required: true, trigger: "blur", message: "请填写数额" }],
        pid: [{ required: true, trigger: "blur", message: "请填写物品id" }],
        tags: [{ required: true, trigger: "blur", message: "请设置标签" }],
      },
    };
  },

  watch: {},

  async mounted() {},

  methods: {
    upload() {
      let tags = this.form.tags.split(" ");
      if (tags.length < 3) {
        Notification.error({
          title: "拒绝",
          message: "请补充完整的标签",
          duration: 2000,
        });
        return;
      }

      this.$refs.uploadForm.validate((valid) => {
        if (!valid) return;

        const userName = getters.userName();
        const value = this.form.value;
        const pid = this.form.pid;
        numberApi
            //上传承诺值
          .create({ pid, value, userName, tags})
          .then((res) => {
            Message({
              message: "上传成功",
              duration: 5000,
              type: "success",
            });
            console.log(res);
          })
          .catch((e) => {
            Message({
              message: e.message,
              duration: 5000,
              type: "error",
            });
          });
      });
    },
  },
};
</script>
